.Home{
    height:100vh;
    width:100vw;
    
    align-content: center;
    display: flex;
    .header{
        margin: 0;
    }
    
}

.main {
    width: 100vw;
    height: 100vh;
    background: #164e8b;
    margin: auto;
    //box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
    border-radius: 3.8px;
    background: linear-gradient(270deg, rgba(73,11,102,1) 65%, rgba(186,110,214,1) 66%);
    background-size: cover;
    display: flex;
    flex-direction: row;
    @media (max-width:650px) {
        flex-direction: column;
}
}